"use client";
import { useState } from "react";
import { FiUser,
        FiPhone,
        FiBookOpen,
        FiBriefcase,
        FiAward,
        FiPlus,
        FiLogOut,
        FiSettings} from "react-icons/fi";
import { GoArrowSwitch } from "react-icons/go";


export default function Editor() {
    const[toggle, setToggle] = useState(<div></div>);

    function activateButton(name: string){
        switch (name) {
            case "personalInfo":
                return setToggle(<div>pInfo</div>)
            case "contactInfo":
                return setToggle(<div>cInfo</div>)
        }
    }


    return(
        <div className="grid grid-cols-7 w-screen h-screen bg-darkGray">
            {/*Left side*/}
            <div className="col-span-2 flex flex-row m-[6px] rounded-[6px] bg-mediumGray">
                {/*Buttons*/}
                <div className="flex flex-col p-[6px] w-[15%]">
                    {/*Top*/}
                    <div className="flex flex-col justify-start items-center w-full h-full gap-[6px]">
                        {/*Personal Info*/}
                        <button onClick={() => activateButton("personalInfo")}
                                className="flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100">
                            <FiUser className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Contact Info*/}
                        <button onClick={() => activateButton("contactInfo")}
                                className="flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100">
                            <FiPhone className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Education*/}
                        <button className="flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100">
                            <FiBookOpen className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Work Experience*/}
                        <button className="flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100">
                            <FiBriefcase className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Skills/Awards*/}
                        <button className="flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100">
                            <FiAward className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Add Section*/}
                        <button className="flex justify-center w-fit h-fit rounded-[4px] border-[2px] border-highlightGray p-2 hover:bg-highlightGray transition-bg duration-100">
                            <FiPlus className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                    </div>
                    {/*Bottom*/}
                    <div className="flex flex-col items-center w-full h-fit gap-[6px]">
                        {/*Swap*/}
                        <button className="flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100">
                            <GoArrowSwitch className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Settings*/}
                        <button className="flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100">
                            <FiSettings className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Exit*/}
                        <button className="flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100">
                            <FiLogOut className="text-lightRed scale-x-[-1] w-[25px] h-[25px]"/>
                        </button>
                    </div>
                </div>
                {/*Line*/}
                <div className="bg-darkGray w-[4px] bg-darkGray"></div>
                {/*Content*/}
                {toggle && <div>
                    { toggle }
                </div>}
            </div>

            <div className="col-span-3">

            </div>

            <div className="col-span-2 bg-white">

            </div>
        </div>
    )
}